.App {
    text-align: left;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: top;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.editPanel {
    position: absolute;
    top: 0px;
    right: 0px;
    height: 100%;
    background: "black";
}

.field {
    display: block;
}

.multiline {
    resize: none;
}

.action {
    font-size: large;
    border: white 2px solid;
    color: white;
    background-color: black;
    display: block;
    width: 100%;
}
.action:hover {
    color: black;
    background-color: white;
}
